import React from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import bannerleft from '../components/image/banner-left.webp';
import bannerright from '../components/image/banner-right.webp';
import france from '../components/image/france.jpg';
import england from '../components/image/england.jpg';

const Top = () => {
    return (
        <Container>
            <div className="banner banner-left">
                <img src={bannerleft} alt="Banner Left" />
            </div>
            <div className="middle">
                <div className="flag-container">
                    <img src={france} alt="France" className="flag" />
                    <span className="arrow">&#8594;</span>
                    <img src={england} alt="England" className="flag" />
                </div>
                <h1 className="title">
                    French video with English subtitles
                </h1>
                <p className="description">
                    Want your videos to reach a wider audience who speaks English? This service is ideal for your audiovisual content - from online videos, television shows, films, documentaries, marketing content and more!
                </p>
                <Link to='/submitpage'>
                    <button className="button">Get Started</button>
                </Link>
            </div>
            <div className="banner banner-right">
                <img src={bannerright} alt="Banner Right" />
            </div>
        </Container>
    );
}

const Container = styled.div`
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    padding:10px;

    .banner {
        flex: 1;
        text-align: center;

        img {
            width: 300px;
            height: auto;
        }
    }

    .middle {
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .flag-container {
        display: flex;
        align-items: center;
        margin-bottom: 20px;
    }

    .flag {
        width: 150px;
        height: auto;
        margin: 0 20px;
    }

    .arrow {
        font-size: 40px;
    }

    .title {
        font-size: 36px;
        text-align: center;
        margin-bottom: 20px;
    }

    .description {
        font-size: 18px;
        text-align: center;
        margin-bottom: 20px;
    }

    .button {
        background-color: blue;
        color: white;
        padding: 10px 20px;
        border: none;
        border-radius: 5px;
        cursor: pointer;
        font-size: 18px;
        transition: background-color 0.3s;

        &:hover {
            background-color: darkblue;
        }
    }

    .banner-left {
        text-align: left;
    }

    .banner-right {
        text-align: right;
    }

    @media only screen and (max-width: 768px) {
        .banner-left,
        .banner-right {
            display: none;
        }
    }
`;

export default Top;
