import React from 'react';
import styled from 'styled-components';
import feature1 from '../components/image/feature-1.webp';

const HowTo = () => {
  return (
    <Container>
      <img src={feature1} alt="Feature 1" />
      <div className='content'>
        <h1>How to create English subtitles for a French video?</h1>
        <ul>
          <li><h2>1. Upload your file.</h2></li>
          <li><p>With our uploader, you can import your file from anywhere, whether it's on your laptop, Google Drive, Youtube, or Dropbox.</p></li>
          <li><h2>2. Select "French".</h2></li>
          <li><p>Determine the language of the audio of your video.</p></li>
          <li><h2>3. Select "Human made".</h2></li>
          <li><p>With our Human-Made service, the original (French) subtitles will be created by a native subtitler and proofread by an experienced reviewer.</p></li>
          <li><h2>4. Toggle "Translate subtitles to another language".</h2></li>
          <li><p>Select "English" to determine the target language for the subtitle translation process.</p></li>
          <li><h2>5. Get notified when your French and English subtitles are ready.</h2></li>
          <li><p>Receive an email when the subtitles have been created within 24 hours for the French subtitles and 3-5 days for English subtitles.</p></li>
          <li><h2>6. Receive your subtitles.</h2></li>
          <li><p>Access your subtitles from the dashboard.</p></li>
          <li><h2>7. Click on "Export" and choose your preferred file format.</h2></li>
          <li><p>You can export to SRT, VTT, and many more. It's that easy to have your video professionally subtitled!</p></li>
        </ul>
      </div>
    </Container>
  );
}

const Container = styled.div`
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  padding: 50px;

  img {
    width: 300px;
    height: auto;
    margin-right: 50px;
  }

  .content {
    background-color: #f4f4f4;
    padding: 20px;
    border-radius: 8px;
    max-width: 600px;
  }

  h1 {
    font-size: 24px;
    margin-bottom: 20px;
  }

  h2 {
    font-size: 20px;
    margin-top: 20px;
    margin-bottom: 10px;
  }

  p {
    font-size: 16px;
    margin-bottom: 20px;
  }

  ul {
    list-style: none;
    padding: 0;
  }

  li {
    margin-bottom: 20px;
  }

  @media (max-width: 600px) {
    flex-direction: column;

    img {
      margin-right: 0;
      margin-bottom: 20px;
    }

    .content {
      max-width: 100%;
    }
  }
`;

export default HowTo;
