import React from 'react';
import styled from 'styled-components';
import Achivement1 from '../components/image/achivement-1.svg';
import Achivement2 from '../components/image/achivement-2.svg';
import brand1 from '../components/image/brand-1.png';
import brand2 from '../components/image/brand-2.png';
import brand3 from '../components/image/brand-3.svg';
import brand4 from '../components/image/brand-4.svg';
import brand5 from '../components/image/brand-5.png';

import { Rating } from '@mantine/core';

const Achivements = () => {
    return (
        <Container>
            <div className='img'>
                <img src={Achivement1} alt="Achievement 1" />
                <img src={Achivement2} alt="Achievement 2" />
            </div>

            <div className='rating'>
                <Rating value={4.5} fractions={2} readOnly />
                <p>Rated <b>4.8/5</b> based on 850+ reviews</p>
            </div>

            <div className='brands'>
                <div className='brand'>
                    <img src={brand1} alt="Brand 1" />
                    <img src={brand2} alt="Brand 2" />
                    <img src={brand3} alt="Brand 3" />
                </div>
                <div className='brand'>
                    <img src={brand4} alt="Brand 4" />
                    <img src={brand5} alt="Brand 5" />
                </div>
                <p className="brand-text">Trusted by <b>100,000+</b> users and teams of all sizes.</p>
            </div>

        </Container>
    );
}

const Container = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  .img {
    display: flex;
    justify-content: space-between;
    align-items: center;

    img {
      width: 100px; 
      height: auto;
    }
  }

  .rating {
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
  }

  .brands {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
  }

  .brand {
    display: flex;
    justify-content: space-between;
    align-items: center;

    img {
      margin: 20px;
      width: 100px;
      height: auto;
    }
  }

  .brand-text {
    text-align: center;
  }

  @media only screen and (max-width: 768px) {
    .brand {
      flex-wrap: wrap;
      justify-content: center;
      img {
        width: 80px;
        margin: 10px;
      }
    }
  }
`;

export default Achivements;
